@import url(https://fonts.googleapis.com/css2?family=Macondo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: rgb(238, 208, 151);
  width: 100vmax;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: background-color 1s;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.appContainer {
  display: flex;
  flex-flow: column wrap;
  width: 100%;
  justify-content: center;
  align-items: center;
  position: relative;
  left: 0%;
}
@media only screen and (min-width: 1000px) {
  .App {
    background-color: rgb(245, 183, 69);
    
  }
  
}

.container{
    background-color: rgb(247, 236, 215);
    width: 200px;
    height: 220px;
    margin: 10px;
    border-radius: 8px;
    
}

.description{
    width: 100%;
    margin: 2px;
    text-align: center;
    font-size: 1.3rem;
    
    
    
    
    
}


.image{
    position: relative;
    left: 40px;
    width: 120px;
    
    
    background-size: contain;
    background-repeat: no-repeat;
    text-align: center;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all 1s;
    
    

}

.image:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2)

}
.image p {
    padding-top: 140px;
    font-size: 1.5rem;
}


*{
    font-family: 'Macondo', cursive;
}

.cardsContainer{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    width: 450px;
    background-color: rgb(248, 159, 43);
    border-radius: 8px;
    margin-bottom: 200px;
    transition: width 1s;
}
.languages{
    width: 400px;
    font-size: 2.5rem;
}
@media only screen and (min-width: 1000px) {
    .cardsContainer {
      width: 900px;
      border-radius: 30px;
      box-shadow: 6px 7px 7px rgb(243, 207, 207);
      
    }
    .languages{
        width: 800px;
    }
}
